require('../config/Config.js');

const styles = [{"featureType":"all","elementType":"labels.text.fill","stylers":[{"saturation":36},{"color":"#000000"},{"lightness":40}]},{"featureType":"all","elementType":"labels.text.stroke","stylers":[{"visibility":"on"},{"color":"#000000"},{"lightness":16}]},{"featureType":"all","elementType":"labels.icon","stylers":[{"visibility":"off"}]},{"featureType":"administrative","elementType":"geometry.fill","stylers":[{"color":"#000000"},{"lightness":20}]},{"featureType":"administrative","elementType":"geometry.stroke","stylers":[{"color":"#000000"},{"lightness":17},{"weight":1.2}]},{"featureType":"landscape","elementType":"geometry","stylers":[{"color":"#000000"},{"lightness":20}]},{"featureType":"poi","elementType":"geometry","stylers":[{"color":"#000000"},{"lightness":21}]},{"featureType":"road.highway","elementType":"geometry.fill","stylers":[{"color":"#000000"},{"lightness":17}]},{"featureType":"road.highway","elementType":"geometry.stroke","stylers":[{"color":"#000000"},{"lightness":29},{"weight":0.2}]},{"featureType":"road.arterial","elementType":"geometry","stylers":[{"color":"#000000"},{"lightness":18}]},{"featureType":"road.local","elementType":"geometry","stylers":[{"color":"#000000"},{"lightness":16}]},{"featureType":"transit","elementType":"geometry","stylers":[{"color":"#000000"},{"lightness":19}]},{"featureType":"water","elementType":"geometry","stylers":[{"color":"#000000"},{"lightness":17}]}];


const icon = {
	url: `${config.url}image/icon/map-marker.png`,
	size: {
		width: 35,
		height: 46,
	},
	scaledSize: {
		width: 35 * 2 / 3,
		height: 46 * 2 / 3,
	},
};

class Map{

	constructor(wrapper){
		this._wrapper = wrapper;
		this._element = this._wrapper.querySelector('.offices__map');

		this._infoWindow = null;
		this._map = null;

		this._initialize = this._initialize.bind(this);
		this._showInfoWindows = this._showInfoWindows.bind(this);
		this._addMarkers = this._addMarkers.bind(this);
		
	}


	init(){

		window.initialize = this._initialize;
		this._loadScript(this._cb);

	}


	_initialize(){

		this._initMap();
		
		if(offices.length !== 0){
			this._addMarkers();
		} else {
			offices.push({
				title: 'Capital Federal',
				content:'Capital Federal',
				lat: -34.620245,
				lng: -58.502938,
				zIndex: 1
			})
		}

		this._centerMap();
		this._initInfoWindows();

	}

	_initInfoWindows(){
		this._infoWindow = new google.maps.InfoWindow();
	}

	_initMap(){
		this._map = new google.maps.Map(this._element, {
			center: {lat: -34.397, lng: 150.644},
			zoom: 14,
			scrollwheel: false,
			minZoom: 12, 
			maxZoom: 16,
			styles,
		});
		//console.log("init")
		let map = this._map;
	 	let minZoomLevel = 10;
	 	let maxZoomLevel = 16;
	}

	_addMarkers(){
		//console.log(offices)
		offices.map((office, index) => {

			//console.log(office)

			let marker = new google.maps.Marker({
				position: {lat: office.lat, lng: office.lng},
				map: this._map,
				title: office.title,
				zIndex: office.zIndex,
				icon,
			});
			marker.addListener('click', () => this._showInfoWindows(index, marker));
		})
	}

	_showInfoWindows(index, marker) {
		let content = offices[index].content;
		let html = 
		`<div class="infowindow">
			${content}
		</div>`;
		this._infoWindow.close();
		this._infoWindow.setContent(html);
		this._infoWindow.open(this._map, marker);
	}

	_centerMap(){

    	let latlngbounds = new google.maps.LatLngBounds();

		offices.map((office) => {
   			latlngbounds.extend(new google.maps.LatLng (office.lat, office.lng));
		});

		this._map.setCenter(latlngbounds.getCenter());
		this._map.fitBounds(latlngbounds); 
	}


	_loadScript(){

		let key = 'AIzaSyAO_ZdDDZ7Bu3qZEWi_nasE9gbvJ42FE2I';

		let script = document.createElement('script');
    	script.type = 'text/javascript';
    	document.getElementsByTagName('head')[0].appendChild(script);
    	script.src = 'http://maps.googleapis.com/maps/api/js?key=' + key + '&v=3&callback=initialize';

	}

	_addEventsListeners(){

	}

}

module.exports = Map;



