var Thematic = require('../component/Thematic');
var CustomSelect = require('../component/CustomSelect');
var $ = require('jquery');
require('../config/Config');
var ModalContact = require('../component/ModalContact');
var Favorites = require('../component/Favorites');
var SendContact = require('../component/SendContact');


class Search{

	constructor(){
		this._wrapper = document.querySelector('.results__list');
		this._buttonSelect = this._wrapper.querySelectorAll('.item__select');
		this._eventSelectOption = this._selectOption.bind(this);
		this._modalContact = new ModalContact(
            document.querySelector('.component__contact'),
            document.querySelectorAll('.js-open-contact'),
        )
        this._sendContact = new SendContact();
	}

	init(){
		new Thematic().init();
		new CustomSelect('.results__order').init();
		this._addEventsListeners();
        this._modalContact.init();
        new Favorites().init();
        this._sendContact.init();
	}

	_selectOption(event){
		let target = event.currentTarget;
		let slug = target.getAttribute('data-slug');
		let title = target.getAttribute('data-title');
		if(!$(target).hasClass('js-open-user-login')){
			if(!$(target).hasClass('js-selected-franchise')){
				$(target).addClass('js-selected-franchise');
			} else {
				$(target).removeClass('js-selected-franchise');
			}

			this._sendInfo(slug);
		}
	}

	_sendInfo(slug){

		$.ajax({
			method: "POST",
			data: {
				_token: config.token,
				slug_franchise: slug
			},
			dataType: "json",
		    url: config.url + "addFranchise",
		    success: (result)=>{
				let total = result.data.quantity;
				let slugs = result.data.slugs;
				this._sendContact.changeTotal(total);
				this._sendContact.open();
				this._modalContact.sendSlugs(slugs);
		    },
		    error: (result) => {
				alert('Hubo un error al enviar la informacin');
		    }
		});
	}

	_addEventsListeners(){
		for(let i = 0; i <= this._buttonSelect.length - 1; i ++){
			this._buttonSelect[i].addEventListener('click', this._eventSelectOption);
		}
	}
}

module.exports = Search;


