/**
 * Detect css transition end with Javascript
 * Modernizr - Davidwalsh
 * https://davidwalsh.name/css-animation-callback
 */
/* From Modernizr */

/*
- How to use
var transitionEvent = window.whichTransitionEvent();
transitionEvent && element.addEventListener(transitionEvent, function() {
    console.log('Transition complete!  This is the callback, no library needed!');
});
*/
window.whichTransitionEvent = function whichTransitionEvent(){
    var t;
    var el = document.createElement('fakeelement');
    var transitions = {
        'transition':'transitionend',
        'OTransition':'oTransitionEnd',
        'MozTransition':'transitionend',
        'WebkitTransition':'webkitTransitionEnd'
    }

    for(t in transitions){
        if( el.style[t] !== undefined ){
            return transitions[t];
        }
    }
}



