require('../style/main.css');

var Home = require('./page/Home');
var FranchiseItem = require('./page/FranchiseItem');
var Adviser = require('./page/Adviser');
var Search = require('./page/Search');
var Profile = require('./page/Profile');
var NewsItem = require('./page/NewsItem');
var Franchising = require('./page/Franchising');
var Header = require('./component/Header');
var Footer = require('./component/Footer');
var ModalUserLogin = require('./component/ModalUserLogin');
var ModalUserRegister = require('./component/ModalUserRegister');
var ModalCompare = require('./component/ModalCompare');
var ModalShare = require('./component/ModalShare');
var ModalContactInfo = require('./component/ModalContactInfo');
var Config = require('./config/Config.js');

var PageManagement = (function () {
    function init(){
        new ModalUserLogin(
            document.querySelector('.component__user-login'),
            document.querySelectorAll('.js-open-user-login'),
        ).init();
        new ModalUserRegister(
            document.querySelector('.component__user-register'),
            document.querySelectorAll('.js-open-user-register'),
        ).init();

        new ModalShare(
            document.querySelector('.component__share'),
            document.querySelectorAll('.js-open-share'),
        ).init();

        new ModalCompare(
            document.querySelector('.component__compare'),
            document.querySelectorAll('.js-open-compare'),
        ).init();

        new ModalContactInfo(
            document.querySelector('.component__contact-info'),
            document.querySelectorAll('.js-open-contact-info'),
        ).init();




        new Header().init();  
        new Footer().init();  
        detectCurrentPage();
    }
 
    function detectCurrentPage(){
        let currentPage = Config.currentPage;
        switch(currentPage){
            case 'home':
                var _currentPage = new Home();
                _currentPage.init();
                break;
            case 'franchise-item':
                var _currentPage = new FranchiseItem();
                _currentPage.init();
                break;
            case 'adviser':
                var _currentPage = new Adviser();
                _currentPage.init();
                break;
            case 'search':
            case 'subjects-item':
            case 'thematics-item':
                var _currentPage = new Search();
                _currentPage.init();
                break;
            case 'profile':
                var _currentPage = new Profile();
                _currentPage.init();
                break;
            case 'news-item':
                var _currentPage = new NewsItem();
                _currentPage.init();
                break;
            case 'franchising':
                var _currentPage = new Franchising();
                _currentPage.init();
                break;


        }
    }
 
    init();

})();


