var Modal = require('./Modal');
var TweenMax = require('TweenMax');
var $ = require('jquery');
require('../config/Config');

class ModalCompare extends Modal{
	

	constructor(wrapper,buttonOpenModal){
		super(wrapper);
		this._wrapper = wrapper;
		this._ifLoaded = false;
		this._totalSelected = 0;
		this._total = 3;

		//Buttons
		this._buttonOpenModal = buttonOpenModal;
		this._buttonCloseModal = this._wrapper.querySelectorAll('.js-close-modal');
		this._buttonCompare = $('.compare__button__compare');
		this._buttonBack = $('.compare__button__back');
		
		//Wrappers
		this._wrapperAllItems = $('.compare__list--all');
		this._wrapperSelected = $('.compare__list--selected');
		this._wrapperList = $('.list--items__wrapper');

		//Events
		this._eventOpenModal = this._openModal.bind(this);
		this._eventCloseModal = this._closeModal.bind(this);
	}


	init(){
		this._addEventsListeners();
	}

	_addCompare(selected){
		let item = $(".compare__item[data-slug='" + selected +"']");
		let slug = item.attr('data-slug');
		let name = item.attr('data-name');
		let image = item.attr('data-image');
		item.addClass("js-active");
		

		let falseDiv = `
			<div class="list--items__item list--items__item--compare js-active" data-slug="${slug}" data-name="${name}">
            	<i style="background-image:url('${image}')"></i>
            </div>
        `
        this._wrapperList.append($(falseDiv));
        setTimeout(()=>{
	        TweenMax.set($(".list--items__item--compare[data-slug='" + slug +"']"),{
	        	opacity:1,
	        	scale:1
	        });
    	},10)

	}

	_removeAllCompare(data){
		$('.list--items__item--compare').remove();
		$('.list--items__item--compare').removeClass('js-active');
		$(".compare__item").removeClass('js-active');
		$(".compare__item").removeClass('js-not-select');
		this._totalSelected = 0;

		TweenMax.delayedCall(0.5,()=>{

			$('.compare__list--selected').find('.compare__list--selected__wrapper').empty();


			TweenMax.set('.compare__list--all',{
				y:0,
				opacity:1,
				display:"block"
			});

			this._buttonCompare.prop("disabled", true);

			TweenMax.set('.compare__list--selected',{
				opacity:0,
				display:"none"
			});


			TweenMax.to(this._wrapperList,0.5,{
				opacity:1,
			});

			TweenMax.to(this._buttonCompare,0.5,{
				opacity:1,
				visibility:"visible"
			});

			TweenMax.to(this._buttonBack,0.5,{
				opacity:0,
				onComplete:()=>{
					TweenMax.set(this._buttonBack,{
						display:"none"
					})
				}
			});


		});
	}

	_removeCompareList(){
		TweenMax.staggerTo($('.compare__list').find('.selected__item'),0.75,{
			y:-10,
			opacity:0
		},0.1);


		TweenMax.to(this._buttonBack,0.5,{
			opacity:0,
			onComplete:()=>{
				TweenMax.set(this._buttonBack,{
					display:"none"
				})
			}
		});

		TweenMax.delayedCall(1,()=>{

			TweenMax.set('.compare__list--all',{
				y:10,
				display:'block'
			});

			$('.compare__list--selected').find('.compare__list--selected__wrapper').empty();
			
			TweenMax.set('.compare__list--selected',{
				display:'none'
			});

			TweenMax.to('.compare__list--all',0.5,{
				y:0,
				opacity:1
			});

			TweenMax.to(this._wrapperList,0.5,{
				opacity:1,
			});

			TweenMax.to(this._buttonCompare,0.5,{
				opacity:1,
				visibility:"visible"
			});


		});
	}

	_createCompareList(list,number){

		for(let i = 0; i <= list.length - 1; i ++){

			let item = list[i];

			let falseDiv = $(`
			
				<div class="selected__item selected__item--type-${number}">
					<div class="seletect__item__image">
						<i class="item__img" style="background-image:url('${config.url}uploads/${item.logo}')"></i>
					</div>

					<ul class="selected__item__list">
						<li class="selected__item__info">Nombre</li>
						<li class="selected__item__info">${(item.name === '') ? '-' : item.name}</li>


						<li class="selected__item__info">Pas de origen</li>
						<li class="selected__item__info">${(item.country === '') ? '-' : item.country}</li>

						<li class="selected__item__info">Pases con presencia</li>
						<li class="selected__item__info">${(item.country_in === '') ? '-' : item.country_in}</li>

						<li class="selected__item__info">Apertura primera franquicia</li>
						<li class="selected__item__info">${(item.grand_open === '') ? '-' : item.grand_open}</li>

						<li class="selected__item__info">Franquicias otorgadas el ltimo ao</li>
						<li class="selected__item__info">${(item.franchises_this_year === '') ? '-' : item.franchises_this_year}</li>

						<li class="selected__item__info">Canon/Fee de ingreso</li>
						<li class="selected__item__info">${(item.fee === '') ? '-' : item.fee}</li>

						<li class="selected__item__info">Inversin total</li>
						<li class="selected__item__info">${(item.total_investment === '') ? '-' : item.total_investment}</li>

						<li class="selected__item__info">Regalas/Royalty</li>
						<li class="selected__item__info">${(item.royalty === '') ? '-' : item.royalty}</li>

						<li class="selected__item__info">Publicidad corporativa</li>
						<li class="selected__item__info">${(item.corporate_advertising === '') ? '-' : item.corporate_advertising}</li>

						<li class="selected__item__info">Canon de publicidad</li>
						<li class="selected__item__info">${(item.canon_advertising === '') ? '-' : item.canon_advertising}</li>

						<li class="selected__item__info">Financiacin disponible</li>
						<li class="selected__item__info">${(item.financing_available === '') ? '-' : item.financing_available}</li>

						<li class="selected__item__info">Facturacin promedio anual por local</li>
						<li class="selected__item__info">${(item.average_annual === '') ? '-' : item.average_annual}</li>

						<li class="selected__item__info">Recupero estimado</li>
						<li class="selected__item__info">${(item.recover_estimated === '') ? '-' : item.recover_estimated}</li>

						<li class="selected__item__info">Dimensin mnima del local</li>
						<li class="selected__item__info">${(item.premises_size === '') ? '-' : item.premises_size}</li>

						<li class="selected__item__info">Empleados por local</li>
						<li class="selected__item__info">${(item.employees === '') ? '-' : item.employees}</li>

						<li class="selected__item__info">Vigencia del contrato</li>
						<li class="selected__item__info">${(item.contract_term === '') ? '-' : item.contract_term}</li>

						<li class="selected__item__info">Franquicia exportable</li>
						<li class="selected__item__info">${(item.exportable_franchise === '') ? '-' : item.exportable_franchise}</li>

					</ul>
				</div>
			`);

			this._wrapperSelected.find('.compare__list--selected__wrapper').append(falseDiv);

		}

		TweenMax.to(this._wrapperList,0.5,{
			opacity:0,
		});

		TweenMax.to(this._buttonCompare,0.5,{
			opacity:0,
			visibility:"hidden"
		});


		TweenMax.to('.compare__list--all',0.65,{
			opacity:0,
			y: -10,
			onComplete:()=>{
				TweenMax.set('.compare__list--all',{
					display:'none'
				});

				TweenMax.set(['.compare__list--selected',this._buttonBack],{
					display:'block',
					opacity:1
				});

				TweenMax.staggerTo($('.compare__list').find('.selected__item'),0.75,{
					y:0,
					opacity:1
				},0.1);
			}
		})
	}

	_sendInfoCompareList(){
		let items = $('.list--items__item--compare');
		let info = '';
		$(".compare__item").removeClass('js-not-select');
		$('.compare__block').css('visibility','visible');
		TweenMax.to('.compare__spinner--button',0.5,{
			opacity:1
		});
		$('.compare__list--selected__wrapper').attr('data-lenght',items.length);
		for(let i = 0; i <= items.length - 1; i ++){
			info = info + items[i].getAttribute('data-slug') + ((i < items.length - 1) ? '/' : ' ');
		}

		super.sendInfo(
			(data)=>{
				TweenMax.to('.compare__spinner--button',0.5,{
					opacity:0
				});
				$('.compare__block').css('visibility','hidden');
			},

			(data)=>{
				this._createCompareList(data,items.length);
				TweenMax.to('.compare__spinner--button',0.5,{
					opacity:0
				});
				$('.compare__block').css('visibility','hidden');

			},
			'GET',
			`franquicia/comparar/${info}`
		);

	}

	_createFullList(selected){

		TweenMax.to('.compare__spinner--center',0.5,{
			opacity:1
		});

		super.sendInfo(
			(data)=>{
				TweenMax.to('.compare__spinner--center',0.5,{
					opacity:0
				});

			},

			(data)=>{
				this._ifLoaded = true;
				for(let i = 0; i <= data.length - 1; i++){
					let falseDiv = `
						<li class="compare__item" data-image="${config.url}uploads/${data[i].logo}" data-name="${data[i].name}" data-slug="${data[i].slug}">
							<i class="item__img" style="background-image:url('${config.url}uploads/${data[i].logo}')"></i>
							<div class="item__wrapper">
								<svg class="item__icon" width="100%" height="100%" viewBox="0 0 100 100">
									<use xlink:href="#svg-symbol__compare"></use>
								</svg>
								<p class="item__text">comparar</p>
							</div>
						</li>
					`

					this._wrapperAllItems.append($(falseDiv));

					TweenMax.to('.compare__spinner--center',0.5,{
						opacity:0
					});
				}
				this._totalSelected = this._totalSelected + 1;
				this._addCompare(selected);
			},
			'GET',
			'franquicia/lista/comparar'
		);

	}

	_toggleSelected(event){
		let target = $(event.currentTarget);
		let slug = target.attr('data-slug');
		if(target.hasClass('js-active')){
			this._totalSelected = this._totalSelected - 1;
			target.removeClass('js-active');
			$(".compare__item[data-slug='" + slug +"']").removeClass('js-active');
			$(".list--items__item--compare[data-slug='" + slug +"']").remove();
			$('.list--item__item--false').css("display","block");
		} else {
			if(this._totalSelected === this._total - 1){
				$('.list--item__item--false').css("display","none");
			}

			if(this._totalSelected < this._total){
				target.addClass('js-active');
				this._addCompare(slug);		
				this._totalSelected = this._totalSelected + 1;		
			} else {
				target.removeClass('js-not-select');
				setTimeout(function(){
					target.addClass('js-not-select');
				},100)
			}


		}

		if(this._totalSelected > 1){
			this._buttonCompare.prop("disabled", false);
		} else {
			this._buttonCompare.prop("disabled", true);
		}

	}

	_openModal(event){
		event.preventDefault();
		let target = event.currentTarget;
		let slug = target.getAttribute('data-slug');
		if(!this._ifLoaded){
			this._createFullList(slug);
		} else {
			this._removeAllCompare();
			console.log(slug);
			this._addCompare(slug);
			this._totalSelected = 1;
			$('.list--item__item--false').css("display","block");
		}
		super.open();
		console.log(1);
		$("body").addClass("js-hidden-scroll");
	}

	_closeModal(event){
		event.preventDefault();
		this._removeAllCompare();
		super.close();
		$("body").removeClass("js-hidden-scroll");
	}

	_resize(){
		//TODO
		if($('.selected__item__info')){
		}
	}

	_addEventsListeners(){
		for(let i = 0; i <= this._buttonOpenModal.length - 1; i ++){
			this._buttonOpenModal[i].addEventListener('click', this._eventOpenModal);
		}
		for(let i = 0; i <= this._buttonCloseModal.length - 1; i ++){
			this._buttonCloseModal[i].addEventListener('click', this._eventCloseModal);
		}

		$(document).on('click','.compare__item',this._toggleSelected.bind(this));

		$(document).on('click','.list--items__item--compare',this._toggleSelected.bind(this));

		$(document).on('click','.compare__button__compare',this._sendInfoCompareList.bind(this));

		$(document).on('click','.compare__button__back',this._removeCompareList.bind(this));
	
		$(window).on('resize',this._resize.bind(this));
	}

}

module.exports = ModalCompare;


