var $ = require('jquery');

class SendContact{

	constructor(){
		this._wrapper = document.querySelector('.component__send-contact');
		this._buttonClose = this._wrapper.querySelector('.send-contact__close'); 
		this._totalContact = this._wrapper.querySelector('.total-contact');

		this._total = this._totalContact.innerText;
		//Event
		this._eventClose = this.close.bind(this);
	}

	init(){
		this._addEventsListeners();
		if(this._total > 0){
			this.open();
		}
	}

	open(){
		if(this._total === 0){
			this.close();
		} else {
			$(this._wrapper).addClass('js-active');
		}
	}

	close(event){
		event.preventDefault();

		$.ajax({
			method: 'POST',
			data: $( this._wrapper ).serialize(),
			dataType: "json",
			context: this._form,
		    url: config.url + 'clearFranchise',
		    success: (result)=>{
				$(this._wrapper).removeClass('js-active');
				$('.results__item').find('.item__select').removeClass('js-selected-franchise');
		    },
		    error: (result) => {
				alert('Hubo un error al enviar la informacin');
		    }
		});

	}

	changeTotal(number){
		this._totalContact.innerText = number;
		this._total = number;
	}

	_addEventsListeners(){
		this._buttonClose.addEventListener('click', this._eventClose);
	}

}

module.exports = SendContact;


