var $ = require('jquery');
require('../config/Config');
class Favorites{

	constructor(){
		this._elements = document.querySelectorAll('.button-favorites');
		this._eventToggleButton = this._toggleButton.bind(this);
	}

	init(){
		this._addEventsListeners();
	}

	_toggleButton(event){
		let target = $(event.currentTarget);
		let itemNumber = target.attr('data-item');
		if(!target.hasClass('js-open-user-login')){
			this._sendInfo(itemNumber,target);
		}
	}

	_sendInfo(itemNumber,target){
		$.ajax({
			method: "POST",
			data: {
				_token: config.token,
				franchise: itemNumber
			},
			dataType: "json",
		    url: config.url + "addFavorite",
		    success: (result)=>{
				if(target.hasClass('js-active')){
					
					if(config.currentPage === 'franchise-item'){
						$(this._elements).removeClass('js-active');
					} else {
						target.removeClass('js-active');
					}

				} else {
					if(config.currentPage === 'franchise-item'){
						$(this._elements).addClass('js-active');
					} else {
						target.addClass('js-active');
					}
				}
		    },
		    error: (result) => {
				alert('Hubo un error al enviar la informacin');
		    }
		});

	}

	_addEventsListeners(){
		for(let i = 0; i <= this._elements.length - 1; i++){
			this._elements[i].addEventListener('click', this._eventToggleButton);
		}
	}

}

module.exports = Favorites;


