var $ = require('jquery');
var TweenMax = require('TweenMax');
require('../config/Config');
class Newsletter{
	constructor(wrapper){
		this._wrapper = wrapper;
		this._form = this._wrapper.querySelector('.newsletter__form');
		this._inputEmail = this._form.querySelector('.newsletter__input');
		this._button = this._form.querySelector('.newsletter__btn');

		this._spinner = this._form.querySelector('.spinner');

		//Events
		this._eventIfValid = this._ifValid.bind(this);
		this._eventSendInfo = this._sendInfo.bind(this);
	}

	init(){
		this._addEventsListeners();
	}

	_validateEmail(email) {
    	var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    	return re.test(email);
	}

	_ifValid(event){
		event.preventDefault();
		let target = event.currentTarget;
		let validateEmail = this._validateEmail(target.value);
		if(validateEmail){
			this._inputEmail.setAttribute('data-validate', 'true');
		} else {
			this._inputEmail.setAttribute('data-validate', 'false');
		}
	}

	_sendInfo(event){
		event.preventDefault();
		let validateEmail = this._validateEmail(this._inputEmail.value);
		if(!validateEmail){
			return false;
		}

		TweenMax.to(this._spinner,0.5,{
			opacity:1
		});

		$.ajax({
			method: "POST",
			data: $( this._form ).serialize(),
			dataType: "json",
			context: this._form,
		    url: config.url + "addNewsletter",
		    success: (result)=>{
				TweenMax.to(this._spinner,0.5,{
					opacity:0
				});
		    },
		    error: (result) => {
				TweenMax.to(this._spinner,0.5,{
					opacity:0
				});			
		    }
		});
	}

	_addEventsListeners(){
		this._inputEmail.addEventListener('keyup', this._eventIfValid);
		this._button.addEventListener('click', this._eventSendInfo);
	}

}

module.exports = Newsletter;


