var $ = require('jquery');
var TweenMax = require('TweenMax');
require('../config/Config');


class Modal{
	constructor(wrapper){
		this._wrapper = wrapper;
		this._form = this._wrapper.querySelector('.modal__form');
		this._wrapperBlock = this._wrapper.querySelector('.compare__block');
		this._url = (this._form) ? this._form.getAttribute('data-url') : '';
		this._spinner = this._wrapper.querySelector('.spinner');
	}

	open(wrapper = this._wrapper){
		TweenLite.set(wrapper,{
			display:'block',
			onComplete:()=>{
				TweenLite.to(wrapper,0.65,{
					opacity:1
				});
				TweenLite.fromTo(wrapper.querySelector('.modal__wrapper'),0.35,
					{
						y:20
					},
					{
						y:0,
						opacity:1
					}
				);
			}
		});
	}

	close(wrapper = this._wrapper){

		TweenLite.to(wrapper,0.65,{
			opacity:0,
			delay:0.5,
			onComplete:()=>{
				TweenLite.set(wrapper,{
					display:'none'
				});
			}
		});

		TweenLite.to(wrapper.querySelector('.modal__wrapper'),0.35,{
			y:-20,
			opacity:0
		});
	}

	sendInfo(statusFalse,statusTrue,method="POST",url = this._url){

		if(this._spinner){
			TweenMax.to(this._spinner,0.5,{
				opacity:1
			});
		}


		$.ajax({
			method: method,
			data: $( this._form ).serialize(),
			dataType: "json",
			context: this._form,
		    url: config.url + url,
		    success: (result)=>{

		    	if(this._spinner){
					TweenMax.to(this._spinner,0.5,{
						opacity:0
					});				
				}

				if(result.data.status === false){
					statusFalse(result.data.message);
				} else {
					statusTrue(result.data.message);
				}
		    },
		    error: (result) => {
		    	if(this._spinner){
					TweenMax.to(this._spinner,0.5,{
						opacity:0
					});
				}

				if(this._wrapperBlock){
					this._wrapperBlock.style.visibility = "hidden";
				}

				alert('Hubo un error al enviar la informacin');
		    }
		});


	}
}

module.exports = Modal;



