var $ = require('jquery');
require('../config/Config');

class Header{
	constructor(){
        this._element = document.querySelector(".header");
		this._hamburgerBtn = this._element.querySelector('.header__hamburger');
		this._linkChangeUrl = this._element.querySelectorAll('.js-change-url');
		this._buttonToggleSubMenu = this._element.querySelectorAll('.js-open-sub-menu');
		this._eventToggleMenu = this._toggleMenu.bind(this);
		this._eventResetMenu = this._resetMenu.bind(this);
		this._eventToggleSubMenu = this._toggleSubMenu.bind(this);
	}

	init(){
		this._resetMenu();
		this._addEventsListeners();
	}

	_toggleMenu(event){

		let target = event.currentTarget;
		let ifOpen = this._element.getAttribute('data-open');
		if(window.innerWidth < 1024){
			if(ifOpen === 'false'){
				this._element.setAttribute('data-open', 'true');
				document.body.setAttribute('data-open-menu', 'true');
			} else {
				this._element.setAttribute('data-open', 'false');
				document.body.setAttribute('data-open-menu', 'false');
			}
		} 
	}

	_toggleSubMenu(event){
		let target = event.currentTarget;
		let ifOpen = target.getAttribute('data-open');

		if(config.userAgent.device === 'desktop'){
			if(window.innerWidth >= 1024){
				return false;
			}
		}

		if(ifOpen === 'false'){
			target.setAttribute('data-open', 'true');
		} else {
			target.setAttribute('data-open', 'false');
		}

	}

	_resetMenu(){
		
		if(config.userAgent.device === 'desktop'){
			if(window.innerWidth >= 1024){
				for(let i = 0; i <= this._linkChangeUrl.length - 1; i ++){
					let url = this._linkChangeUrl[i].getAttribute('data-url');
					this._linkChangeUrl[i].setAttribute('href',url);
				}
			} else {
				for(let i = 0; i <= this._linkChangeUrl.length - 1; i ++){
					let url = this._linkChangeUrl[i].getAttribute('data-url');
					$(this._linkChangeUrl[i]).removeAttr("href");
				}				
			}
		} 

		$('.js-open-sub-menu').attr('data-open','false');

		if(window.innerWidth >= 1024){
			document.body.setAttribute('data-open-menu', 'false');
			this._element.setAttribute('data-open', 'false');
		}
	}

	_addEventsListeners(){
		window.addEventListener('resize',this._eventResetMenu);
		this._hamburgerBtn.addEventListener('click', this._eventToggleMenu, false);

		for(let i = 0; i <= this._buttonToggleSubMenu.length - 1; i ++){
			this._buttonToggleSubMenu[i].addEventListener('click', this._eventToggleSubMenu, false);
		}
		
		$('.icon__social--whatsapp').on('click',function(){
			$(this).toggleClass('js-active')
		});
	}

	_removeEventsListeners(){
		window.removeEventListener('resize',this._eventResetMenu);
		this._hamburgerBtn.removeEventListener('click', this._eventToggleMenu, false);
	}

	destruct(){
		this._removeEventsListeners();
	}
}


module.exports = Header;


