var $ = require('jquery');
var TweenMax = require('TweenMax');
class NewsItem{

	constructor(){
		this._wrapper = $('.section__news-item');
		this._wrapperFixed = this._wrapper.find('.news__wrapper__right');
		this._banner = this._wrapper.find('.bottom__banner');
		this.width = this._wrapperFixed.width();
		this._eventFixedContent = this._fixedContent.bind(this);
	}

	init(){
		window.requestAnimationFrame(this._eventFixedContent);

		this.resizeNewsImages();
		$(window).on('resize', this.resizeNewsImages);
	}

	_fixedContent(){
		let left;

		if(window.innerWidth >= 1700){
			left = "calc(50% - -460px)";
		} else {
			left = "77%";
		}

		if(window.innerWidth >= 1024){
			var top = $(".news__wrapper").offset().top;
			let wrapperFixedPosY = top + this._wrapperFixed.innerHeight();
			let bannerPosY = this._banner.offset().top - window.pageYOffset;


			if(wrapperFixedPosY < window.innerHeight){

				if(bannerPosY <= wrapperFixedPosY){
					this._wrapperFixed.css({"position":"absolute","bottom":0,"top":"inherit","left":'inherit'});
					TweenMax.set(this._wrapperFixed,{
						x: "0%" 
					});
				} else {
					this._wrapperFixed.css({"position":"fixed","top":top + "px","bottom":"inherit","left":left});
					TweenMax.set(this._wrapperFixed,{
						x: "-50%"
					});

				}

			} else {
				this._wrapperFixed.css({"position":"static","top":0,"bottom":"inherit","left":'inherit'});
				TweenMax.set(this._wrapperFixed,{
					x: "0%"
				});

			}


		} else {
			this._wrapperFixed.css({"position":"static","top":0,"bottom":"inherit","left":'inherit'});
			TweenMax.set(this._wrapperFixed,{
				x: "0%"
			});

		}
		
		window.requestAnimationFrame(this._eventFixedContent);
	}
	
	_elementInViewport(el) {
		var top = el.offsetTop;
		var left = el.offsetLeft;
		var width = el.offsetWidth;
		var height = el.offsetHeight;

		while(el.offsetParent) {
			el = el.offsetParent;
			top += el.offsetTop;
			left += el.offsetLeft;
		}

		return (
			top < (window.pageYOffset + window.innerHeight) &&
			left < (window.pageXOffset + window.innerWidth) &&
			(top + height) > window.pageYOffset &&
			(left + width) > window.pageXOffset
		);
	}

	resizeNewsImages() {
	    $('.last-news__img').each(function(index, item){
	        var $item = $(item);
	        var targetHeight = $item.siblings('.last-news__info').outerHeight() - 15;
	        if ($('body').prop('clientWidth') > 1007) {
	            $item.outerHeight(targetHeight);
	        } else if ($('body').prop('clientWidth') > 746){
	            $item.outerHeight(targetHeight);
	            $item.css('padding-bottom', 0);
	        } else {
	            $item.css('height', 'auto');
	        }
	    });
	}

	_addEventsListeners(){

	}


}

module.exports = NewsItem;


