var uaParser = require("ua-parser-js");


function userAgent(){

    const _UA = new uaParser();
    const _UAResult = _UA.getResult();

    let _userAgent = {};
    
    _userAgent.browser = _UAResult.browser.name;
    _userAgent.browserVersion = _UAResult.browser.major;
    _userAgent.osVersion = _UAResult.os.version;

    if(_UAResult.device.model === undefined){
        _userAgent.platform = _UAResult.os.name;
        _userAgent.device = 'desktop';
    } else {
        _userAgent.os = _UAResult.os.name;
        _userAgent.device = _UAResult.device.type;
        _userAgent.platform = _UAResult.device.model;
        _userAgent.deviceVendor = _UAResult.device.vendor;
    }

    return {
        browser: _userAgent.browser,
        browserVersion: _userAgent.browserVersion,
        osName: _userAgent.os,
        osVersion: _userAgent.osVersion,
        platform: _userAgent.platform,
        device: _userAgent.device,
        deviceVendor: _userAgent.deviceVendor
    }

}


module.exports = userAgent();


