var Modal = require('./Modal');
var TweenMax = require('TweenMax');
require('../config/Config');

class ModalShare extends Modal{
	

	constructor(wrapper,buttonOpenModal){
		super(wrapper);
		this._wrapper = wrapper;

		//Data
		this.dataUrl = '';
		this.dataTitle = '';
		this.dataText = '';
		this.dataVia = '';

		//Buttons
		this._buttonOpenModal = buttonOpenModal;
		this._buttonCloseModal = this._wrapper.querySelectorAll('.js-close-modal');
		this._buttonsShare = this._wrapper.querySelectorAll('.js-share');
		
		//Events
		this._eventOpenModal = this._openModal.bind(this);
		this._eventCloseModal = this._closeModal.bind(this);
		this._eventShare = this._share.bind(this);
	}


	init(){
		this._addEventsListeners();

		if(config.userAgent.device === 'desktop'){
			let whatsapp = this._wrapper.querySelector('.social--whatsapp');
			whatsapp.remove();
		}

	}

	_openModal(event){
		event.preventDefault();
		let target = event.currentTarget;
		this.dataUrl = target.getAttribute('data-url');
		this.dataTitle = target.getAttribute('data-title');
		this.dataText = target.getAttribute('data-text');
		this.dataVia = target.getAttribute('data-via');
		super.open();
	}



	_shareFacebook(){
		window.open('https://www.facebook.com/sharer/sharer.php?u='+this.dataUrl+'&title='+this.dataTitle+'', 'pop', 'height=450, width=550, top='+(window.innerHeight/2 - 225) +', left='+window.innerWidth/2 +', toolbar=0, location=0, menubar=0, directories=0, scrollbars=0');
	}

	_shareTwitter(){
		window.open('http://twitter.com/share?text=' + this.dataTitle + '&via='+ this.dataVia +'&url=' + this.dataUrl + '&', 'twitterwindow', 'height=450, width=550, top='+(window.innerHeight/2 - 225) +', left='+window.innerWidth/2 +', toolbar=0, location=0, menubar=0, directories=0, scrollbars=0');
	}

	_shareMail(){
		var formattedBody = "Franquicia "+this.dataTitle+" \n "+this.dataUrl+" \n ";
		var mailToLink = "mailto:?body=" + encodeURIComponent(formattedBody)+"&Subject=Franquicia "+this.dataTitle+"";
		window.location.href = mailToLink;
	}

	_shareWhatsapp(){
		let link = 'whatsapp://send?text=Te comparto la franquicia '+this.dataTitle+' '+this.dataUrl+'';
		window.location.href = link;
	}

	_share(event){
		event.preventDefault();
		let target = event.currentTarget;
		let _share = target.getAttribute('data-share');

		if(_share === 'facebook'){
			this._shareFacebook();
		} else if(_share === 'twitter'){
			this._shareTwitter();
		} else if(_share === 'mail'){
			this._shareMail();
		} else if(_share === 'whatsapp'){
			this._shareWhatsapp();
		}
	}

	_closeModal(event){
		event.preventDefault();
		super.close();
	}

	_addEventsListeners(){
		for(let i = 0; i <= this._buttonOpenModal.length - 1; i ++){
			this._buttonOpenModal[i].addEventListener('click', this._eventOpenModal);
		}
		for(let i = 0; i <= this._buttonCloseModal.length - 1; i ++){
			this._buttonCloseModal[i].addEventListener('click', this._eventCloseModal);
		}

		for(let i = 0; i <= this._buttonsShare.length - 1; i ++){
			this._buttonsShare[i].addEventListener('click', this._eventShare);
		}

	}

}

module.exports = ModalShare;


