var Modal = require('./Modal');
var TweenMax = require('TweenMax');
var $ = require('jquery');

class ModalContact extends Modal{

	constructor(wrapper,buttonOpenModal){
		super(wrapper);
		this._wrapper = wrapper;

		//Labels
		this._labels = this._wrapper.querySelectorAll('.form__label__required');

		//Error message
		this._errorMessage = this._wrapper.querySelectorAll('.form__error')


		//Ok message
		this._okMessage = this._wrapper.querySelector('.form__ok');

		//Buttons
		this._buttonOpenModal = buttonOpenModal;
		this._buttonCloseModal = this._wrapper.querySelectorAll('.js-close-modal');
		this._buttonSendInfo = this._wrapper.querySelector('.form__button--send');
		
		//Events
		this._eventOpenModal = this._openModal.bind(this);
		this._eventCloseModal = this._closeModal.bind(this);
		this._eventSendInfo = this._sendInfo.bind(this);
	}


	init(){
		this._addEventsListeners();
	}

	_openModal(event){
		event.preventDefault();
		super.open();
	}

	_closeModal(event){
		event.preventDefault();

		super.close();
	}

	_sendInfo(event){
		event.preventDefault();
		if(window.IsRecapchaValid()){
			super.sendInfo(
				(data)=>{
					console.log(data);
					for (let prop in data) { 
						for(let i = 0; i <= this._labels.length - 1; i ++){
							let label = this._labels[i].getAttribute('data-label');
							if(prop === label){
								this._labels[i].setAttribute('data-error', 'true');
								this._errorMessage[i].innerText = data[prop][0];
							}
						}
					}
				},

				(data)=>{
					for(let i = 0; i <= this._labels.length - 1; i ++){
						this._labels[i].setAttribute('data-error', 'false');
					}

					for(let i = 0; i <= this._errorMessage.length - 1; i ++){
						this._errorMessage[i].innerText = '';
					}
					this._okMessage.style.display = 'inline-block';
				}
			);
		}
	}

	_addEventsListeners(){
		for(let i = 0; i <= this._buttonOpenModal.length - 1; i ++){
			this._buttonOpenModal[i].addEventListener('click', this._eventOpenModal);
		}
		for(let i = 0; i <= this._buttonCloseModal.length - 1; i ++){
			this._buttonCloseModal[i].addEventListener('click', this._eventCloseModal);
		}

		this._buttonSendInfo.addEventListener('click',this._eventSendInfo);

		$(".contact-info__radio").on("click",function(){
			let target = $(this);
			$('.contact-info__radio').removeClass('js-active');
			if(target.hasClass('js-active')){
				$(this).removeClass('js-active');
			} else {
				$(this).addClass('js-active');
			}
		});

	}
}


module.exports = ModalContact;


