var $ = require('jquery');
var TweenMax = require('TweenMax');

class VideoModal{
	constructor(){
		this._element = document.querySelector('.component__video');
		this._elementWrapper = this._element.querySelector('.video__wrapper');
		this._iframe = null;
		this._back = this._element.querySelector('.video__back');
		this._buttonClose = this._element.querySelector('.video__close');
		this._title = this._element.querySelector('.video__title').querySelector('span');
		this._buttons = document.querySelectorAll('.js-open-video');
		this._eventOpenModal = this._openModal.bind(this);
	}

	init(){
		this._addEventsListeners();
	}

	_openModal(event){
		let target = event.currentTarget;
		let id = target.getAttribute('data-video-id');
		let title = target.getAttribute('data-video-title');
		this._title.innerHTML = title;
		this._createVideo(id);
		this._elementWrapper.style.opacity = 0;
		TweenLite.set(this._element,{
			display:'block',
			onComplete:()=>{
				TweenLite.to(this._element,1,{
					opacity:1
				});
				TweenLite.fromTo(this._elementWrapper,0.75,
					{
						y:20
					},
					{
						y:0,
						opacity:1
					}
				);
			}
		});
	}

	_closeModal(){
		TweenLite.to(this._element,1,{
			opacity:0,
			delay:0.5,
			onComplete:()=>{
				TweenLite.set(this._element,{
					display:'none'
				});
				this._iframe.remove();
				this._iframe = null;
				this._title.innerHTML = '';
				this._elementWrapper.setAttribute('style', '');
			}
		});

		TweenLite.to(this._elementWrapper,0.75,{
			y:-20,
			opacity:0
		});
	}

	_createVideo(id){
		this._iframe = document.createElement('iframe');
		this._iframe.src = `https://www.youtube.com/embed/${id}`;
		this._elementWrapper.querySelector('.video__iframe').appendChild(this._iframe);
	}

	_addEventsListeners(){
		for(let i = 0; i <= this._buttons.length - 1; i++){
			this._buttons[i].addEventListener('click', this._eventOpenModal);
		}
		
		this._buttonClose.addEventListener('click', this._closeModal.bind(this));
		this._back.addEventListener('click', this._closeModal.bind(this));

	}

}

module.exports = VideoModal;


