var $ = require('jquery');
var slick = require('../lib/slick');

class Thematic {

	constructor(){
		this._elements = document.querySelectorAll('.thematic__item');
		this._totalElements = this._elements.length;
	}

	init(){
    $('.thematic__list').slick({
        arrows:false,
        slidesToShow: (this._totalElements <= 8) ? this._totalElements : 8,
        infinite: true,
        responsive: [
            {
              breakpoint: 1023,
              settings: {
                slidesToShow: (this._totalElements <= 2) ? this._totalElements : 2,
                slidesToScroll: 1,
                infinite: true,
                arrows:true
              }
            },
            {
              breakpoint: 767,
              settings: {
                slidesToShow: 1,
                slidesToScroll: 1,
                infinite: true,
                arrows:true
              }
            }                  
        ]            
    });
	}
}

module.exports = Thematic;


