var $ = require('jquery');
require('../config/Config');

class Profile {
	constructor(){
		this._wrapper = document.querySelector('.section__profile');
		this._spinner = this._wrapper.querySelector('.spinner');
		this._form = this._wrapper.querySelector('form');
		this._button = this._wrapper.querySelector('.form__button');
		this._labels = this._wrapper.querySelectorAll('.form__label__required');
		this._errorMessage = this._wrapper.querySelectorAll('.form__error');
		this._okMessage = this._wrapper.querySelector('.form__ok');

		//Events
		this._eventSendInfo = this._sendInfo.bind(this);
	}

	init(){
		this._addEventsListeners();
	}

	_sendInfo(event){
		event.preventDefault();

		TweenMax.to(this._spinner,0.5,{
			opacity:1
		});

		$.ajax({
			method: "POST",
			data: $( this._form ).serialize(),
			dataType: "json",
			context: this._form,
		    url: config.url + 'editUser',
		    success: (result)=>{
		    	let data = result.data.message;
		    	console.log(result);

				TweenMax.to(this._spinner,0.5,{
					opacity:0
				});				

				if(result.data.status === false){

					for (let prop in data) { 
						console.log(prop);
						for(let i = 0; i <= this._labels.length - 1; i ++){
							let label = this._labels[i].getAttribute('data-label');
							if(prop === label){
								this._labels[i].setAttribute('data-error', 'true');
								this._errorMessage[i].innerText = data[prop][0];
							}
						}
					}

				} else {

					for(let i = 0; i <= this._labels.length - 1; i ++){
						this._labels[i].setAttribute('data-error', 'false');
					}

					for(let i = 0; i <= this._errorMessage.length - 1; i ++){
						this._errorMessage[i].innerText = '';
					}
					this._okMessage.style.display = 'inline-block';

				}
		    },
		    error: (result) => {
				TweenMax.to(this._spinner,0.5,{
					opacity:0
				});

				alert('Hubo un error al enviar la informacin');
		    }
		});

	}

	_addEventsListeners(){
		this._button.addEventListener('click', this._eventSendInfo);
	}
}

module.exports = Profile;


