var $ = require('jquery');
var TweenMax = require('TweenMax');
require('../config/Config');
class Survey{

	constructor(wrapper){
		this._wrapper = wrapper;
		this._form = this._wrapper.querySelector('.survey__form');
		this._options = this._form.querySelectorAll('.survey__radio');
		this._spinner = this._form.querySelector('.spinner');
		this._line = this._form.querySelectorAll('.survey__line__back');
		this._percentage = this._form.querySelectorAll('.survey__number--visible');
		this._percentageHide = this._form.querySelectorAll('.survey__number--hidden');
		
		this._eventSendInfo = this._sendInfo.bind(this);
	}

	init(){
		this._addEventsListeners();
		this._detectIfVoted();
	}

	_sendInfo(event){
		if(this._form.getAttribute('data-voted') !== 'voted'){
			let target = event.currentTarget;
			let falseRatio = target.nextElementSibling;
			falseRatio.setAttribute('data-cheked', 'true');

			this._form.setAttribute('data-voted','voted');

			TweenMax.to(this._spinner,0.5,{
				opacity:1
			});		

			$.ajax({
				method: "POST",
				data: $( this._form ).serialize(),
				dataType: "json",
				context: this._form,
			    url: config.url + 'vote',
			    success: (result)=>{
			    	
					for(let i = 0; i <= this._percentageHide.length - 1; i++){
						let percentage = result.data.results[i].percentage;
						this._percentageHide[i].innerText = `${percentage}%`;
						this._line[i].style.width = `${percentage}%`;
					}

					TweenMax.to(this._spinner,0.5,{
						opacity:0
					});

					TweenMax.staggerTo(this._percentage,0.25,{
						opacity:0,
						y: -5
					},0.03);

					TweenMax.staggerTo(this._percentageHide,0.45,{
						opacity:1,
						delay:0.15,
						y: 0
					},0.035);

			    },
			    error: (result) => {
					TweenMax.to(this._spinner,0.5,{
						opacity:0
					});		
					falseRatio.setAttribute('data-cheked', 'false');
					this._form.setAttribute('data-voted','');	
					target.checked = false;				
			    }
			});
		}
	}

	_detectIfVoted(){
		for(let i = 0; i <= this._options.length - 1; i++){
			if(this._options[i].checked){
				let falseRatio = this._options[i].nextElementSibling;
				falseRatio.setAttribute('data-cheked', 'true');
			}
		}
	}

	_addEventsListeners(){
		for(let i = 0; i <= this._options.length - 1; i++){
			this._options[i].addEventListener('click', this._eventSendInfo);
		}
	}


}

module.exports = Survey;


