var $ = require('jquery');
var slick = require('../lib/slick');
var Survey = require('../component/Survey');
var Newsletter = require('../component/Newsletter');
var Sponsors = require('../component/Sponsors');
var Thematic = require('../component/Thematic');
var VideoModal = require('../component/VideoModal');
var CustomSelect = require('../component/CustomSelect');

class Home{
 

    constructor(){
        this._element = document.querySelector(".section__home");
        this._survey = null;
        this._newsletter = null;
    }
     
    init(){
    	this._initSlick();
        new CustomSelect('.select__thematics').init();
        new CustomSelect('.select__investments').init();
        this.transitionIn();
        this._survey = new Survey(this._element);
        this._survey.init();
        this._newsletter = new Newsletter(this._element);
        this._newsletter.init();
        new Sponsors().init();
        new Thematic().init();
        new VideoModal().init();
        this.resizeNewsImages();

        $(window).on('resize', this.resizeNewsImages);
    }

    _initSlick(){
    	$('.home__banner-top').slick({
    		arrows:false,
            dots:true,
            infinite:true,
            autoplay: true,
            autoplaySpeed: 3500,
            speed: 800,
            fade: true,
            cssEase: 'linear',
            pauseOnHover:false
    	});        

        $('.weekly__slider').slick({
            arrows:true,
            dots:true,
            autoplay:true,
            autoplaySpeed: 1500,
            speed: 800,
            infinite:true,
            slidesToShow: 1
        });
    }

    resizeNewsImages() {
        $('.last-news__img').each(function(index, item){
            var $item = $(item);
            var targetHeight = $item.siblings('.last-news__info').outerHeight() - 15;
            if ($('body').prop('clientWidth') > 1007) {
                $item.outerHeight(targetHeight);
            } else if ($('body').prop('clientWidth') > 746){
                $item.outerHeight(targetHeight);
                $item.css('padding-bottom', 0);
            } else {
                $item.css('height', 'auto');
            }
        });
    }

    transitionIn(){
    
    }
}

module.exports = Home;


