var userAgent = require("./userAgent");
require('../polyfill/polyfill');

class Config{
    
    constructor(){
        this.userAgent = {
            browser: userAgent.browser,
            browserVersion: userAgent.browserVersion,
            osName: userAgent.osName,
            osVersion: userAgent.osVersion,
            platform: userAgent.platform,
            device: userAgent.device,
            deviceVendor: userAgent.deviceVendor
        };
        this.url = document.body.getAttribute('data-url');
        this.currentPage = document.body.getAttribute('data-section');
        this.token = document.querySelector('meta[name="_token"]').content
    }

    init(){
        this.setAttrBrowser(); 
    }
 

    setAttrBrowser(){
        const body = document.body;
        body.setAttribute("data-browser",this.userAgent.browser);
        body.setAttribute("data-browserVersion",this.userAgent.browserVersion);
        body.setAttribute("data-osName",this.userAgent.osName);
        body.setAttribute("data-osVersion",this.userAgent.osVersion);
        body.setAttribute("data-platform",this.userAgent.platform);
        body.setAttribute("data-device",this.userAgent.device);
        body.setAttribute("data-deviceVendor",this.userAgent.deviceVendor);
    }
}


window.config = new Config();
window.config.init();
module.exports = window.config;


