var $ = require('jquery');
var TweenMax = require('TweenMax');

class Adviser{
	constructor(){
		this._element = document.querySelector('.section__adviser');
		this._form = document.querySelector('.adviser__form');
		this._button = this._element.querySelector('.form__btn');
		this._textArea = this._element.querySelector('.form__textarea');
		this._options = this._element.querySelectorAll('.adviser__form__item');
		this._eventSelectOption = this._selectOption.bind(this);
		this._eventValidateTextArea = this._validateTextArea.bind(this);
		this._eventSendInfo = this._sendInfo.bind(this);
	}

	init(){
		this._addEventsListeners();
	}

	_selectOption(event){
		let target = event.currentTarget;
		let state = target.getAttribute('data-active');
		if(state === 'true'){

		} else {
			for(let i = 0; i <= this._options.length - 1; i++){
				this._options[i].setAttribute('data-active','false');
				this._options[i].setAttribute('data-validate','true');
			}			
			target.setAttribute('data-active','true');
			setTimeout(function() {
			  $('.form__textarea').focus();
			}, 0)

		}
	}
 
	_validateTextArea(event){
		if(this._textArea.value === ''){
			this._textArea.setAttribute('data-validate', 'false');
			return false;
		} else { 
			this._textArea.setAttribute('data-validate', 'true');
			return true;
		}
	}

	_validateOptions(){
		let validate = [];
		for(let i = 0; i <= this._options.length - 1; i++){
			let state = this._options[i].getAttribute('data-active')
			if(state !== 'true'){
				validate.push(false);
			}
		}		
		if(validate.length - 1 < this._options.length - 1){
			return true;
		} else {
			for(let i = 0; i <= this._options.length - 1; i++){
				this._options[i].setAttribute('data-validate','false');
			}
			return false;
		}
	}

	_sendInfo(event){
		event.preventDefault();
		if(!$(this._button).hasClass('js-open-user-login')){
			if(this._eventValidateTextArea() && this._validateOptions()){
				$.ajax({
					method: "POST",
					data: $( this._form ).serialize(),
					dataType: "json",
					context: this._form,
				    url: "addAdviser",
				    success: (result)=>{
				    },
				    error: (result) => {
								
				    }
				});
			}
		}
	}

	_addEventsListeners(){
		this._textArea.addEventListener('keyup', this._eventValidateTextArea);
		
		this._button.addEventListener('click', this._eventSendInfo);
		for(let i = 0; i <= this._options.length - 1; i++){
			this._options[i].addEventListener('click',this._eventSelectOption);
		}
	}

}

module.exports = Adviser;


