var $ = require('jquery');
var slick = require('../lib/slick');

class Sponsors{
	constructor(){
		this._element = document.querySelectorAll('.sponsors__item');
		this._totalElements = this._element.length;
		this._slideToShow = null;
	}

	init(){

        $('.sponsors__list').slick({
            arrows: true,
            infinite: true,
            autoplay:true,
            autoplaySpeed: 1500,
            speed: 800,
            slidesToShow: (this._totalElements <= 6) ? this._totalElements : 6,
            responsive: [
                {
                  breakpoint: 1500,
                  settings: {
                    slidesToShow: (this._totalElements <= 4) ? this._totalElements : 4,
                    slidesToScroll: 1,
                    infinite: true,
                    arrows:true
                  }
                },

                {
                  breakpoint: 1023,
                  settings: {
                    slidesToShow: (this._totalElements <= 2) ? this._totalElements : 2,
                    slidesToScroll: 1,
                    infinite: true,
                    arrows:true
                  }
                },
                {
                  breakpoint: 767,
                  settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1,
                    infinite: true,
                    arrows:true
                  }
                }                  
            ] 

        });
	}
}

module.exports = Sponsors;


