var $ = require('jquery');
var Map = require('../component/Map');
var ModalMeeting = require('../component/ModalMeeting');
var ModalContact = require('../component/ModalContact');
var ModalAvailableAreas = require('../component/ModalAvailableAreas');
var Favorites = require('../component/Favorites');
class FranchiseItem{

	constructor(){
		this._element = document.querySelector('.section__franchise-item');
		this._hiddenHeader = document.querySelector('.franchise-item__fixed-info');
		this._header = document.querySelector('.header');
		this._info = this._element.querySelector('.info__bottom');
		this._arrow = this._hiddenHeader.querySelector('.fixed-info__arrow');
		this._buttonBack = this._element.querySelector('.js-back');
		this._map = null;
		this._eventToggleHiddenHeader = this._toggleHiddenHeader.bind(this);
		this._eventToggleInfo = this._toggleInfo.bind(this);
		this._oldPosY = 0;
	}


	init(){

		new Favorites().init();

        new ModalMeeting(
            document.querySelector('.component__meeting'),
            document.querySelectorAll('.js-open-meeting'),
        ).init();

        new ModalContact(
            document.querySelector('.component__contact'),
            document.querySelectorAll('.js-open-contact'),
        ).init();

        new ModalAvailableAreas(
            document.querySelector('.component__available-areas'),
            document.querySelectorAll('.js-open-available-areas'),
        ).init();



		this._map = new Map(this._element);
		this._map.init();
		this._cutpasteHiddenHeader();
		this._addEventsListeners();
		window.requestAnimationFrame(this._eventToggleHiddenHeader);
	}


	_back(event){
		event.preventDefault();
		let lastUrl = document.referrer
		window.location.href = lastUrl;
	}


	_cutpasteHiddenHeader(){
		$(this._hiddenHeader).appendTo("body");
	}


	_toggleHiddenHeader() {

		let posY = window.pageYOffset;
		let posYInfo = this._info.getBoundingClientRect().top + this._info.clientHeight;
		if(this._header.getAttribute('data-open') !== 'true'){
			
			if(posY < this._oldPosY){
				this._header.setAttribute('data-hide-scroll', 'false');
				this._hiddenHeader.setAttribute('data-show-info','false');
				
				if(posYInfo > 0){	
					this._hiddenHeader.setAttribute('data-hide-scroll', 'false');
					this._hiddenHeader.setAttribute('data-set-top', 'false');
				} else {
					this._hiddenHeader.setAttribute('data-set-top', 'true');
				}
			} else if(posY > this._oldPosY){
				this._hiddenHeader.setAttribute('data-show-info','false');
				
				if(posYInfo < 0){	
					this._hiddenHeader.setAttribute('data-set-top', 'false');
					this._hiddenHeader.setAttribute('data-hide-scroll', 'true');
					this._header.setAttribute('data-hide-scroll', 'true');
				}
			}
		}

		this._oldPosY = posY;	

	  	window.requestAnimationFrame(this._eventToggleHiddenHeader);
	}

	_toggleInfo(){
		let state = this._hiddenHeader.getAttribute('data-show-info');
		if(state === 'true'){
			this._hiddenHeader.setAttribute('data-show-info','false');
		} else {
			this._hiddenHeader.setAttribute('data-show-info','true');
		}
	}

	_addEventsListeners(){
		this._buttonBack.addEventListener('click',this._back);
		this._arrow.addEventListener('click',this._eventToggleInfo);
	}

}

module.exports = FranchiseItem;


