var $ = require('jquery');
require('../config/Config');
class CustomSelect{

	constructor(wrapper){ 
		this._wrapper = $(document.querySelector(wrapper));
		this._text = this._wrapper.find('.custom-select__text');
		this._select = this._wrapper.find('.custom-select__select');
		this._options = null;
	}

	init(){
		this._addEventsListeners();
		if(config.userAgent.device === 'desktop'){
			this._createList();
		}
	}

	_createList(){
		let items = this._select.find('.js-select');
		let wrapper = $('<ul class="custom-select__list"></ul>')

		for(let i = 0; i <= items.length - 1; i ++){
			wrapper.append(`<li class='custom-select__item' value='${items[i].getAttribute('value')}' data-url='${items[i].getAttribute('data-url')}' data-option='${i}'>${items[i].text}</li>`)
		}

		this._wrapper.append( wrapper );

		this._options = this._wrapper.find('.custom-select__item');
		this._options.on('click', (event)=> {
			let target = $(event.currentTarget)
			let number = target.attr('data-option');
			let value = target.text();
			
			$(this._select.find('.js-select')).attr('selected',false);
			$(this._select.find('.js-select')[number]).attr('selected',true);
			this._text.text( value );

	        switch(config.currentPage){
	            case 'search':
	            case 'subjects-item':
	            case 'thematics-item':		                
	            	let value = $(target).attr('data-url') + $(target).attr('value');
	            	window.location.href = value;
	                break;
	        }


		});

	}

	_addEventsListeners(){

		this._wrapper.on('click', (event)=> {
			event.preventDefault();
			if(config.userAgent.device === 'desktop'){
		  		this._wrapper.toggleClass('js-active');
		  	}
		});

		this._wrapper.on('focusout', (event)=> {
			if(config.userAgent.device === 'desktop'){
		  		this._wrapper.removeClass('js-active');
		  	}
		});

		this._select
	  		.change( (event)=> {
		    	var target = this._select.find(':selected')
			    var value = $(target).text();
		    	this._text.text( value );

		        switch(config.currentPage){
		            case 'search':
		            case 'subjects-item':
		            case 'thematics-item':		                
		            	let value = $(target).attr('data-url') + $(target).attr('value');
		            	window.location.href = value;
		                break;
		        }
	  	});

	}

}


module.exports = CustomSelect;


