var $ = require('jquery');

class Franchising{

	constructor(){
		this._wrapper = $('.section__franchising');
		// this._buttonToggleWrapper = $('.js-toggleWrapper');
		this._bottomListItems = $('.bottom__list__item');
		this._wrapperHidden = $('.wrapperHidden');
		this._viewMoreToggle = $('.viewmore');
	};

	init(){

		// this._buttonToggleWrapper.on("click",()=>{
		// 	this._buttonToggleWrapper.toggleClass("js-active");
		// 	this._wrapperHidden.toggleClass("js-active");
		// });

		this._bottomListItems.each(function(index, item) {
			var $item = $(item);
			if ($item.find('.item__info')[0].scrollHeight <= 195) {
				$item.find('.viewmore').hide();
			}
		});

		this._viewMoreToggle.on('click', function(e) {
			e.preventDefault();
			var $el = $(e.currentTarget);
			if ($el.siblings('.item__info')[0].scrollHeight > 195) {
				$el.siblings('.item__info').toggleClass('open');	
			}
		});
	};

}

module.exports = Franchising;


